import Vue from "vue";
import Vuex from "vuex";
import axios from 'axios';
Vue.use(Vuex);
// 创建vuex实例
const store = new Vuex.Store({
  state: {
    fruitData:[]
  },
  mutations: {
    saveFruitData(state, data) {
      state.fruitData = data
    }
  },
  actions: {
    getFruitData({commit}) {
      axios.get('https://www.easy-mock.com/mock/5a656e61f13806589f3a6ed8/test/list').then(res => {
        commit('saveFruitData', res.data.data)
      }).catch(err => {
        console.log(err);
      })
    }
  }
});
export default store;



